main {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  font-size: 1.2rem;
  font-family: "Space Mono", serif;
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(186deg, 14%, 43%);
}
main .logo {
  margin: 4rem auto;
}
@media (min-width: 52.125rem) {
  main .logo {
    margin: 0 auto 8rem;
  }
}

.container {
  background-color: hsl(0deg, 0%, 100%);
  width: min(100%, 40rem);
  display: grid;
  grid-template-rows: 1fr auto;
  place-items: center;
  border-radius: 1.6rem 1.6rem 0 0;
  padding: 2.4rem;
  gap: 1.6rem;
}
@media (min-width: 52.125rem) {
  .container {
    grid-template-columns: 1fr 1fr;
    width: min(100%, 81.6rem);
    border-radius: 1.6rem;
    padding-bottom: 0;
    gap: 2.4rem;
  }
}

