.inputs__tips .label {
  margin-top: 2.4rem;
}
.inputs__tips .tips-wrapper {
  display: grid;
  place-items: center;
  grid-template-columns: 1fr 1fr;
  gap: 1.6rem;
  margin: 1.6rem 0;
}
@media (min-width: 52.125rem) {
  .inputs__tips .tips-wrapper {
    grid-template-columns: 1fr 1fr 1fr;
  }
}
.inputs__nofp {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 3.2rem auto 0;
}
.inputs__nofp:last-child {
  flex-wrap: wrap;
}
.inputs .error {
  display: none;
  color: hsl(0deg, 100%, 65%);
  font-size: 1.2rem;
}
@media (min-width: 52.125rem) {
  .inputs .error {
    font-size: 1.6rem;
  }
}

