* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  font-size: 62.5%;
}

input,
button {
  border: none;
  outline: none;
}

input,
button {
  font-size: 2.4rem;
  font-family: "Space Mono", serif;
  border-radius: 0.8rem;
  padding: 0.6rem;
  cursor: pointer;
}

.inputs__tips .btn,
.inputs__tips #custom {
  background-color: hsl(183deg, 100%, 15%);
  color: hsl(189deg, 41%, 97%);
  width: 100%;
  max-width: 16.8rem;
}
.inputs__tips .btn::placeholder,
.inputs__tips #custom::placeholder {
  color: hsl(181deg, 24%, 43%);
  text-align: center;
}

input,
#custom {
  background-color: hsl(189deg, 41%, 97%) !important;
  color: hsl(183deg, 100%, 15%) !important;
  text-align: right !important;
  padding-right: 1.6rem;
  cursor: auto;
}

.inputs__bill #bill, .inputs__nofp #bill {
  background-image: url("../../assets/img/icon-dollar.svg");
}
.inputs__bill #nofp, .inputs__nofp #nofp {
  background-image: url("../../assets/img/icon-person.svg");
}
.inputs__bill input, .inputs__nofp input {
  background-repeat: no-repeat;
  background-position: 1.6rem center;
  background-size: 1.2rem;
  width: 100%;
  margin: 0.8rem 0;
}
.inputs__bill input::placeholder, .inputs__nofp input::placeholder {
  color: hsl(181deg, 24%, 43%);
}

.reset .btn {
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
  width: 100%;
  text-transform: uppercase;
}
.reset .is-disable {
  pointer-events: none;
  opacity: 0.4;
}

input:focus {
  outline: 0.2rem solid hsl(172deg, 67%, 45%);
}

.btn:hover {
  background-color: hsl(185deg, 41%, 84%);
  color: hsl(183deg, 100%, 15%);
  opacity: 1;
}

.btn:focus-within:not(.btn--reset) {
  background-color: hsl(172deg, 67%, 45%);
  color: hsl(183deg, 100%, 15%);
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

input[type=number] {
  -moz-appearance: textfield;
}