@import "./base";
@import "./breakpoints";

main {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    font-size: $fs-body;
    font-family: $ff-body;
    background-color: $clr-bg--main;
    color: $clr-label;

    .logo {
        margin: 4rem auto;

        @include breakpoint(medium-up) {
            margin: 0  auto 8rem;
        }
    }
}

.container {
    background-color: $clr-neutral;
    width: min(100%, 40rem);
    display: grid;
    grid-template-rows: 1fr auto;
    place-items: center;
    border-radius: $radius-normal $radius-normal 0 0;
    padding: 2.4rem;
    gap: 1.6rem;

    @include breakpoint(medium-up) {
        grid-template-columns: 1fr 1fr;
        width: min(100%, 81.6rem);
        border-radius: $radius-normal;
        padding-bottom: 0;       
        gap: 2.4rem;
    }
}